.searchbar {
  width: 100%;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.searchbar_input {
  width: 100%;
  margin-left: 18px;
  height: 80%;
  outline: none;
  border: none;
  font-size: 16px;
  line-height: 19.2px;
  letter-spacing: 0.2px;
  color: var(--black-color);
}

.searchbar button {
  height: 100%;
  background-color: transparent;
  border-left: 1px solid var(--black-color);
  padding: 0 18px;
  font-size: 20px;
  cursor: pointer;
}
